import React from "react";
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import ProjectMap from './projectmap.js';
import { Button } from 'react-bootstrap';

class Projects extends React.Component{
  constructor() {
    super();
    this.state = {
      width: 0,
      box_xs: 12,
      pfp_size: '48px',
    };
  }
  // event listener for window size (to determine whether user is in mobile or not).
  componentDidMount() {
    this.handleWindowSizeChange()
    window.addEventListener('resize', this.handleWindowSizeChange);
  }
  componentWillUnmount() {
    window.removeEventListener('resize', this.handleWindowSizeChange);
  }
  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };
  render() {
    const {width} = this.state;
    const isMobile = width <= 1280;
    const paperStyling = {
      height: "100%",
      padding: '40px',
      color: 'white',
      backgroundColor: '#4f5661'
    }
    if (isMobile){
      this.box_xs = 12;
      this.pfp_size = '48px';
    } else {
      this.box_xs = 4;
      this.pfp_size = '64px';
    }
    return (
      <Grid container spacing={3} justify={'space-evenly'}>
        {ProjectMap.map((module) => (
          <Grid item xs={this.box_xs}>
            <Paper style={paperStyling} elevation={4} align={'left'}>
                <h2 style={{fontSize: '150%'}}>
                  {module.title}
                </h2>
              <p style={{fontSize: '80%'}}>{module.date}</p>
              <p>{module.desc}</p>
              {/*<Link to={module.link} target={'_blank'} rel={"noreferrer"}><CodeIcon>Source</CodeIcon></Link>*/}
              <Button variant="contained" style={{backgroundColor: '#4ecca3', color: '#eeeeee'}} href={module.link} target={'_blank'} rel={"noreferrer"}>
                Read More
              </Button>
            </Paper>
          </Grid>
        ))
        }
      </Grid>
    );
  }
}

export default Projects